<template>
  <center>
    <div>
      <table
        border="1"
        cellspacing="1"
        cellpadding="1"
        style="font-family: 'Sunflower', sans-serif; text-align:center"
      >
        <tr>
          <th
            style="width:100px; background-color:#e3fced; color:#000000; font-size:1em"
          >
            번호
          </th>
          <th
            style="width:100px; background-color:#e3fced; color:#000000; font-size:1em"
          >
            ID
          </th>
          <th
            style="width:200px; background-color:#e3fced; color:#000000; font-size:1em"
          >
            제목
          </th>
          <th
            style="width:100px; background-color:#e3fced; color:#000000; font-size:1em"
          >
            글쓴이
          </th>
          <th
            style="width:150px; background-color:#e3fced; color:#000000; font-size:1em"
          >
            날짜
          </th>
          <th
            style="width:100px; background-color:#e3fced; color:#000000; font-size:1em"
          >
            조회수
          </th>
        </tr>
        <tr v-for="qnaone in qnalist" v-bind:key="qnaone.num">
          <td style="text-align:center; font-size:1em">{{ qnaone.num }}</td>
          <td style="text-align:center; font-size:1em">{{ qnaone.id }}</td>
          <td
            style="text-align:center; font-size:1em"
            @click.stop="getQnA(qnaone.num)"
          >
            <a href="#">{{ qnaone.title }}</a>
          </td>
          <td style="text-align:center; font-size:1em">{{ qnaone.name }}</td>
          <td style="text-align:center; font-size:1em">{{ qnaone.wdate }}</td>
          <td style="text-align:center; font-size:1em">{{ qnaone.count }}</td>
        </tr>
      </table>

      <br />
      <br />
      <button
        style="background-color: white; border-radius: 15px; font-family: 'Sunflower', sans-serif; border-color:#e3fced"
      >
        <router-link to="/input">질문 등록</router-link>
      </button>
    </div>
    <br />
    <br />
  </center>
</template>

<script>
import Constant from "../Constant";

export default {
  computed: {
    //store안의 todolist check
    qnalist() {
      return this.$store.state.qnalist;
    }
  },
  created() {
    this.$store.dispatch(Constant.GET_LIST); //action call
  },
  methods: {
    //getTodo, removeTodo, completeTodo
    getQnA(num) {
      this.$router.push("/detail/" + num);
    }
  }
};
</script>
