<template>
  <div class="row" style="font-family: 'Sunflower', sans-serif;">
    <div class="col-md-2"></div>
    <div class="col-md-8">
      <h2 class="text-center" style="font-family: 'Sunflower', sans-serif;">
        질문 내용
        <img
          width="80px"
          height="80px"
          src="http://70.12.108.209:8080/resources/img/animat-sign-post-color.gif"
        />
      </h2>
      <p>&nbsp;</p>
      <div class="table table-responsive">
        <table class="table table-striped">
          <tr>
            <td style="font-weight: bold">작성자</td>
            <td colspan="1">{{ qna.name }}</td>
            <td style="font-weight: bold">ID</td>
            <td colspan="1">{{ qna.id }}</td>
            <td style="font-weight: bold">날짜</td>
            <td colspan="1">{{ qna.wdate }}</td>
          </tr>
          <tr>
            <td style="font-weight: bold">제목</td>
            <td colspan="8">{{ qna.title }}</td>
          </tr>

          <tr>
            <td style="font-weight: bold">글내용</td>
            <td colspan="8">
              <textarea
                name="content"
                style="width: 100%; height: 200px"
                readonly
                v-model="qna.content"
              ></textarea>
            </td>
          </tr>
          <tr>
            <td style="font-weight: bold">답 변</td>
            <td colspan="8" v-if="qna.comment != null">{{ qna.comment }}</td>
            <td colspan="8" v-else>답변이 없습니다.</td>
          </tr>
          <tr>
            <td colspan="6">
              <button
                class="btn btn-warning"
                @click.stop="updateContent(qna.num)"
              >
                수정
              </button>
              &nbsp;
              <button
                class="btn btn-success"
                @click.stop="updateComment(qna.num)"
              >
                답변
              </button>
              &nbsp;
              <button class="btn btn-danger" @click.stop="removeQnA(qna.num)">
                삭제
              </button>
              &nbsp;
              <button class="btn btn-primary" @click.stop="gotoList">
                목록으로
              </button>
            </td>
          </tr>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
import Constant from "../Constant";
export default {
  created() {
    //call getTodo with param
    this.getQnA(this.$route.params.num); // /detail/ +id 로 받으면 이렇게 // /detail/ ?  식으로 받을땐 쿼리
  },
  computed: {
    qna() {
      return this.$store.state.qna;
    }
  },

  methods: {
    getQnA(num) {
      //action call
      this.$store.dispatch(Constant.GET_ONE, { num: num }); // id:id 로 보내면 payload.id 로 받음
    },
    clear() {
      this.qna = {};
    },
    updateContent(num) {
      this.$router.push("/update/" + num);
    },
    updateComment(num) {
      this.$router.push("/comment/" + num);
    },
    removeQnA(num) {
      this.$store.dispatch(Constant.REMOVE_QNA, { num });
      this.$router.push("/list");
    },
    gotoList() {
      this.$router.push("/list");
    }
  }
};
</script>
