<template>
  <div id="app">
    <QnAHeader></QnAHeader>
    <router-view></router-view>
  </div>
</template>

<script>
import Vue from "vue";
//import vue-router
import VueRouter from "vue-router";
import QnAHeader from "./components/QnAHeader.vue";
import QnAInsert from "./components/QnAInsert.vue";
import QnADetail from "./components/QnADetail.vue";
import QnAList from "./components/QnAList.vue";
import QnAUpdateComment from "./components/QnAUpdateComment.vue";
import QnAUpdateContent from "./components/QnAUpdateContent";

Vue.use(VueRouter);

const router = new VueRouter({
  routes: [
    {
      path: "/",
      component: QnAList
    },
    {
      path: "/qna.food",
      component: QnAList
    },
    {
      path: "/input",
      component: QnAInsert
    },
    {
      path: "/list",
      component: QnAList
    },
    {
      path: "/detail/:num",
      component: QnADetail
    },
    {
      path: "/update/:num",
      component: QnAUpdateContent
    },
    {
      path: "/comment/:num",
      component: QnAUpdateComment
    }
  ]
});
export default {
  name: "app",
  router,
  components: {
    QnAHeader
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
