<template>
  <div class="row">
    <div class="col-md-2"></div>
    <div class="col-md-8">
      <h2 class="text-center" style="font-family: 'Sunflower', sans-serif;">
        질문 작성
      </h2>
      <p>&nbsp;</p>
      <div class="table table-responsive">
        <table class="table table-striped">
          <tr>
            <td>ID</td>
            <td>
              <input type="text" :value="curid" size="42" readonly="readonly" />
            </td>
          </tr>
          <tr>
            <td>작성자</td>
            <td>
              <input
                type="text"
                :value="curname"
                size="42"
                readonly="readonly"
              />
            </td>
          </tr>
          <tr>
            <td>제목</td>
            <td>
              <input type="input" v-model="qna.title" size="42" />
            </td>
          </tr>
          <tr>
            <td>질문 내용</td>
            <td colspan="3">
              <textarea
                v-model="qna.content"
                style="width: 100%; height: 200px"
              />
            </td>
          </tr>
          <br /><br />
          <tr>
            <td colspan="3" class="text-center">
              <button class="btn btn-success" @click="addQnA">
                질문 작성
              </button>
              &nbsp;
              <button class="btn btn-primary" @click.stop="gotoList">
                목록으로
              </button>
            </td>
          </tr>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
import Constant from "../Constant";

export default {
  data() {
    return {
      qna: {
        title: "",
        content: ""
      }
    };
  },
  computed: {
    curid() {
      return this.$store.state.curid;
    },
    curname() {
      return this.$store.state.curname;
    }
  },
  created: {
    curid() {
      return this.$store.state.curid;
    },
    curname() {
      return this.$store.state.curname;
    }
  },
  methods: {
    addQnA() {
      //...action call
      this.$store.dispatch(Constant.ADD_QNA, {
        qna: this.qna,
        id: this.curid,
        name: this.curname
      });
      this.$router.push("/list"); //*** 목록 화면으로 넘어감
      this.clear();
    },
    clear() {
      this.qna = {};
    },
    gotoList() {
      this.$router.push("/list");
    }
  }
};
</script>
