import Vue from "vue";
import Vuex from "vuex";
import Constant from "./Constant.js";
import axios from "./axios-common";

Vue.use(Vuex);
const store = new Vuex.Store({
  //state ...
  state: {
    qnalist: [],
    qna: {},
    curid: "",
    curname: ""
  },
  actions: {
    // 외부와의 통신을 통해 데이터 처리 dispath에 의해 호출됨.

    [Constant.GET_LIST]: store => {
      axios
        .get("/qnas")
        .then(response => {
          store.commit(Constant.GET_LIST, { qnalist: response.data });
          console.log(response.data);
        })
        .catch(exp => alert("getQnAList 실패하였습니다." + exp));
    },
    [Constant.UPDATE_CONTENT]: (store, payload) => {
      axios
        .put("/qnascontent", {
          num: payload.qna.num,
          title: payload.qna.title,
          content: payload.qna.content
        })
        .then(response => {
          store.dispatch(Constant.GET_LIST);
          console.log(response.data);
        })
        .catch(exp => alert("COMPULETE 처리에 실패하였습니다." + exp));
    },
    [Constant.UPDATE_COMMENT]: (store, payload) => {
      axios
        .put("/qnascomment", {
          num: payload.qna.num,
          comment: payload.qna.comment
        })
        .then(response => {
          store.dispatch(Constant.GET_LIST);
          console.log(response.data);
        })
        .catch(exp => alert("COMPULETE 처리에 실패하였습니다." + exp));
    },
    [Constant.GET_ONE]: (store, payload) => {
      axios
        .get("/qnas/" + payload.num)
        .then(response => {
          store.commit(Constant.GET_ONE, { qna: response.data });
          console.log(response.data);
        })
        .catch(exp => alert("getQnAOne처리에 실패하였습니다." + exp));
    },

    [Constant.ADD_QNA]: (store, payload) => {
      axios
        .post("/qnas", {
          id: payload.id,
          name: payload.name,
          title: payload.qna.title,
          content: payload.qna.content
        })
        .then(response => {
          store.dispatch(Constant.GET_LIST);
          console.log(response.data);
        })
        .catch(exp => alert("addQnA처리에 실패하였습니다." + exp));
    },
    [Constant.REMOVE_QNA]: (store, payload) => {
      axios
        .delete("/qnas/" + payload.num)
        .then(response => {
          store.dispatch(Constant.GET_LIST);
          console.log(response.data);
        })
        .catch(exp => alert("deleteTodo처리에 실패하였습니다." + exp));
    },
    [Constant.GET_ID]: store => {
      axios
        .get("/qnaID")
        .then(response => {
          store.commit(Constant.GET_ID, {
            curid: response.data.id,
            curname: response.data.name
          });
          console.log(response.data);
        })
        .catch(exp => alert("getID에 실패하였습니다." + exp));
    }
  },
  mutations: {
    // 저장소에 데이터 실제 반영(commit시 호출됨)

    [Constant.GET_LIST]: (state, payload) => {
      state.qnalist = payload.qnalist;
    },
    [Constant.GET_ONE]: (state, payload) => {
      state.qna = payload.qna;
    },
    [Constant.ADD_QNA]: (state, payload) => {
      state.qna = payload.qna;
    },
    [Constant.GET_ID]: (state, payload) => {
      state.curid = payload.curid;
      state.curname = payload.curname;
      console.log(state.curid + " " + state.curname);
    }
  }
});

export default store;
