<template>
  <div class="row">
    <div class="col-md-2"></div>
    <div class="col-md-8">
      <h2 class="text-center" style="font-family: 'Sunflower', sans-serif;">
        질문 수정
        <img
          width="80px"
          height="80px"
          src="http://70.12.108.209:8080/resources/img/animat-checkmark-color.gif"
        />
      </h2>
      <p>&nbsp;</p>
      <div class="table table-responsive">
        <table class="table table-striped">
          <tr>
            <td>ID</td>
            <td>
              <input
                type="text"
                v-model="qna.id"
                size="42"
                readonly="readonly"
              />
            </td>
          </tr>
          <tr>
            <td>작성 시간</td>
            <td>
              <input
                type="input"
                name="wdate"
                size="42"
                v-model="qna.wdate"
                readonly="readonly"
              />
            </td>
          </tr>
          <tr>
            <td>제목</td>
            <td colspan="3">
              <input type="input" v-model="qna.title" size="42" />
            </td>
          </tr>

          <tr>
            <td>질문 내용</td>
            <td colspan="3">
              <textarea
                v-model="qna.content"
                style="width: 100%; height: 200px"
              ></textarea>
            </td>
          </tr>
          <br /><br />
          <tr>
            <td colspan="3" class="text-center">
              <button class="btn btn-success" @click.stop="updateContent">
                질문 수정
              </button>
              &nbsp;
              <button class="btn btn-primary" @click.stop="gotoList">
                목록으로
              </button>
            </td>
          </tr>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
import Constant from "../Constant";

export default {
  created() {
    this.getQnA(this.$route.params.num);
  },
  computed: {
    qna() {
      return this.$store.state.qna;
    }
  },
  methods: {
    getQnA(num) {
      //action call
      this.$store.dispatch(Constant.GET_ONE, { num: num }); // id:id 로 보내면 payload.id 로 받음
    },
    updateContent() {
      this.$store.dispatch(Constant.UPDATE_CONTENT, { qna: this.qna });
      this.$router.push("/detail/" + this.qna.num);
    },
    clear() {
      this.qna = {};
    },
    gotoList() {
      this.$router.push("/list");
    }
  }
};
</script>
